<template>
  <div class="grid">
      <div  v-bind:class="{ hoversize:isThankYou }" class="thankyou" >
        <div class="back-arrow" v-if="isThankYou" v-on:click="toMainContent()"><i class="fa fa-arrow-circle-o-left" ></i></div>
         <div class="thankyou-heading">
          
          Share, <br> Everywhere! 
          
          </div>
          <transition name="fade-1">
          <social-sharing v-if="isThankYou" url="https://vuejs.org/" inline-template>
              <div class="share-f thankyou-share">
                  <network network="facebook">
                      <i class="fa fa-fw fa-facebook"></i>
                  </network> 
              </div> 
          </social-sharing>
          </transition>
          <transition name="fade-2">
          <social-sharing  v-if="isThankYou" url="https://vuejs.org/" inline-template>
              <div class="share-t thankyou-share">
                   <network network="twitter">
                       <i class="fa fa-fw fa-twitter"></i>
                  </network>
              </div> 
          </social-sharing>
          </transition>
          <transition name="fade-3">
           <social-sharing  v-if="isThankYou" url="https://vuejs.org/" inline-template>
              <div class="share-i thankyou-share">
                    <network network="linkedin">
                        <i class="fa fa-fw fa-linkedin"></i> 
                  </network>
              </div> 
          </social-sharing>
          </transition>
          <transition name="fade-4">
           <social-sharing  v-if="isThankYou" url="https://vuejs.org/" inline-template>
              <div class="share-p thankyou-share">
                    <network network="pinterest">
                        <i class="fa fa-fw fa-pinterest"></i> 
                  </network>
              </div> 
          </social-sharing>
          </transition>
          
      </div>
      <transition name="slide-fade-down">"
          <div class="text-content" v-if="isThankYou==false">
              <h1 class="text-heading"><span style="color:#fff; background:red">UNCOVER</span><br> HARASSERS</h1>
              <p class="text-body">Support the MeToo movement by being in the know. Install the chrome extension to highlight the name of celebrities accused of sexual harrasement everytime they appear on your webpage. </p>
              <div class="btn btn-1"> Download</div>
              <div class="btn btn-2" v-on:click="toThankYou()">Share </div>
              <div class="footer">Made with free time by Sarim ✌</div>
          </div>
      </transition>
         <transition name="slide-fade-up">
    <div class="illustration" v-if="isThankYou==false">
    </div>
        </transition>
  </div>
</template>

<script>
export default {
  name: 'HeroSection',
  data () {
    return {
      msg: 'Welcome to Your Vue.js App',
      isThankYou: false
    }
  },
    methods:{
        toThankYou: function(){
            this.isThankYou=true;
        },
             toMainContent: function(){
            this.isThankYou=false;
        
        }
    }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
a {
  color: #42b983;
}
</style>

<style>
    .slide-fade-down-enter-active {
  transition: all .8s ease;
}
.slide-fade-down-leave-active {
  transition: all 0.8s ease;
}
.slide-fade-down-enter, .slide-fade-down-leave-to
/* .slide-fade-leave-active below version 2.1.8 */ {
  transform: translateY(100%);
  opacity: 1;
}
    
        .slide-fade-up-enter-active {
  transition: all .8s ease;
}
.slide-fade-up-leave-active {
  transition: all 0.8s ease;
}
.slide-fade-up-enter, .slide-fade-up-leave-to
/* .slide-fade-leave-active below version 2.1.8 */ {
  transform: translateY(-100%);
  opacity: 1;
}
    
    .fade-1-enter-active{
  transition: all 1s ease;
}
.fade-1-enter{
  transform: translateX(-5px);
  opacity: 0;
}
       .fade-2-enter-active{
  transition: all 1s ease;
}
.fade-2-enter{
 transform: translateX(-60px);
  opacity: 0;
}
       .fade-3-enter-active{
  transition: all 1s ease;
}
.fade-3-enter{
 transform: translateX(-70px);
  opacity: 0;
}
.fade-4-enter-active{
  transition: all 1s ease;
}
.fade-4-enter{
  transform: translateX(-80px);
  opacity: 0;
}
   
    .grid {
        display: grid;
        grid-template-columns: 2fr 3fr;
        justify-items: stretch;
        align-items: stretch;
        height: 100vh;
        grid-template-rows: 1fr;
        overflow: hidden;
    }
    
    .text-content{
        grid-column: 1/2;
        grid-row: 1;
        background-color: black;
        color: white;
        display: grid;
        grid-template-columns: 1fr 2fr 2fr 1fr 2fr 2fr 1fr;
        grid-template-rows: auto;
        padding-top: 50px;
    }
    
    .text-heading{
        grid-column: 2/7;
        text-align: left;
        font-weight: 800;
        font-size: 3em;
        align-self: center;
        
    }
    
    .text-body{
        grid-column: 2/6;
        text-align: left;
        font-size: 1.3em;
        font-weight: 500;
        padding-top: 0px;
        margin-top: 0px;
        margin-bottom: 0px;
        padding-bottom: 0px;
        line-height: 1.3em;
        opacity: 0.9;
        
        
    }
    
    
     .thankyou{
        grid-column: 1/3;
        grid-row: 1;
        background-color: #cfbb39;
        display: grid;
        grid-template-columns: 0.5fr 1fr 1fr 1fr 1fr 0.5fr;
        grid-template-rows: 0.5fr 4fr 3fr 0.5fr;
    }
    
    .thankyou-heading{
        grid-column: 2/6;
        grid-row: 2/3;
        text-align: left;
        color: #fff;
        align-self: center;
        font-size: 10em;
        font-weight: 800;
        line-height: 1em;
    }
    
    .thankyou-share{
      color: #fff;
      align-self: center;
      font-size: 10em;
      font-weight: 800;
      
    }
    
    .hoversize i {
        transition: all .2s ease-in-out; 
    }
    
    .hoversize i:hover{
        transform: scale(1.2);
    }
    
    .share-f{
        grid-row: 3/4;
        grid-column: 2/3;
        background-color: #3b5998;
    }
    
    .share-t{
        grid-row: 3/4;
        grid-column: 3/4;
        background-color: #1da1f2;
    }
    
    .share-i{
        grid-row: 3/4;
        grid-column: 4/5;
        background-color: #0077b5;
    }
    
    .share-p{
        grid-row: 3/4;
        grid-column: 5/6;
        background-color: #bd081c;
    }
    
    .back-arrow{
        grid-row:2/3; 
        grid-column: 1/2;
        text-align: right;
        color: #fff;
        opacity: 0.8;
        font-size: 3em;
    }
    
    .illustration{
        background-image: url("../assets/illustration-v2.svg"), url("../assets/combined-with-drop-big.png");
        background-size: auto, cover;
        background-repeat: no-repeat;
         background-position: right bottom;
        grid-column: 2/5;
        grid-row: 1;
        background-color: #224a54;
    }
    
    .btn-1{
        grid-column: 2/4;
        align-self: center;
    }
    
     .btn-2{
        grid-column: 5/7;
         align-self: center;
    }
    
    .footer{
        grid-column: 1/8;
        align-self: end;
    }
    
    .btn {
        -webkit-border-radius: 4;
        -moz-border-radius: 4;
        border-radius: 4px;
        color: white ;
        font-size: 28px;
        background-color: transparent;
        border: solid 2px white;
        padding: 10px 20px 10px 20px;
        text-decoration: none;
        height: 40px;
        font-weight: 700;
        cursor: pointer;
}

.btn:hover {
        color: #f6f6f7;
        background: #2c3e50;
        text-decoration: none;
}



</style>



